.events-website-page-head {
  padding: 0 10% 0px 10%;
  display: flex;
}

.events-website-page-heading-container {
  width: 40%;
  height: 300px;
}

.events-website-page-title-container {
  display: flex;
}

.events-website-page-mockup-conatiner {
  margin-left: auto;
}

.events-website-tech-stack-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 40% 0 10%;
}

.events-website-features-container {
  margin: 0 5% 0 10%;
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 950px) {
  .events-website-page-mockup-conatiner {
    position: absolute;
    z-index: -999;
    visibility: collapse;
  }

  .events-website-page-heading-container {
    width: 100%;
  }

  .events-website-tech-stack-container {
    margin: 0 10% 0 10%;
  }

  .events-website-features-container {
    justify-content: center;
    margin: 0 5% 0 5%;
  }
}
